.container {
    background-color: #fff;
    max-width: 250px;
    min-height: 250px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin: 2%;
    border-right: 2px solid darkblue;
}

.technology {

}

.technology_logo {
    margin: 0 auto;
    width: 50px;
    height: 50px;
    background-color: lightgrey;
}

.technology_name {
    margin: 10% 0;
    font-size: 20px;
    font-weight: bolder;
}

.description {
    height: 40%;
    border-left: 2px solid darkblue;
    border-top: 2px solid darkblue;
    text-align: center;
    font-weight: bold;
    padding: 0 5%;
}